import React from 'react';
import CreatableSelect from 'react-select/creatable';

import Container from "react-bootstrap/Container";

function createOption(label) {
    return {
        label,
        value: label.toLowerCase().replace(/\W/g, '')
    }
}

let defaultOptions = [
    createOption('One'),
    createOption('Two'),
    createOption('Three'),
];

export default function CreatableSelectComponent({ tags = [], updateTags }) {
    const [isLoading, setIsLoading] = React.useState(false);
    const [options, setOptions] = React.useState(defaultOptions);
    const [value, setValue] = React.useState(undefined);

    function handleChange(newValue, actionMeta) {
        console.group('Value Changed');
        console.log(newValue);
        console.log(`action: ${actionMeta.action}`);
        console.groupEnd();
        setValue(newValue);
    }

    function handleCreate(inputValue) {
        setIsLoading(true);
        console.group('Option created');
        console.log('Wait a moment...');
        setTimeout(() => {
            const newOption = createOption(inputValue);
            updateTags([...options, newOption]);
            console.log(newOption);
            console.groupEnd();
            setOptions(options => [...options, newOption]);
            setValue(newOption);
            setIsLoading(false);
        }, 1000);
    };

    return <Container className="mt-2 mb-2">
        <CreatableSelect
            isMulti
            isClearable
            isDisabled={isLoading}
            isLoading={isLoading}
            onChange={handleChange}
            onCreateOption={handleCreate}
            options={options}
            value={value}
        />
    </Container>

}