import React, { useState } from "react";
import { useSelector } from "react-redux";
import { useForm, FormProvider, useFormContext } from "react-hook-form";
import firebase from "gatsby-plugin-firebase";

import { selectApplication } from "../features/application/applicationSlice";

import CreatableSelectComponent from "../components/CreatableSelectComponent";

import ButtonGroup from "react-bootstrap/ButtonGroup";
import Container from "react-bootstrap/Container";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";


// let formFields = [
//     { id: 0, name: 'h1' },
//     { id: 1, name: 'h2' },
//     { id: 2, name: 'h3' },
//     { id: 3, name: 'h4' },
//     { id: 4, name: 'h5' },
//     { id: 5, name: 'h6' },
//     { id: 6, name: 'last_edit' },
//     { id: 7, name: 'cited_reference' }
// ]

function isObject(val) { if (val === null) { return false; } return ((typeof val === 'function') || (typeof val === 'object')); }

export default function ScienceNotesEditor({ note, screenSize }) {
    const email = useSelector(selectApplication).email;
    const user = useSelector(selectApplication).user;
    const avatarUrl = useSelector(selectApplication)?.avatarUrl;
    const [savedAnswer, setSavedAnswer] = useState(null);
    const methods = useForm({ defaultValues: !!note?.content ? note.content : {} });
    const { handleSubmit } = methods;
    const [currentField, setCurrentField] = useState('last_edit');
    const [tags, setTags] = useState([]);

    function updateTags(newtags) {
        setTags(tags => [...newtags]);
    }

  //  console.log(note);


    const onSubmit = data => {
        console.log(data);

        if (email.length > 6) {
            let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
            let idNote = !!note?.id && note.id !== "new" ? note.id : firebase.database().ref("usersScience/" + userEmail).child("notes").push().key;

            let old_editions = isObject(note?.content?.old_editions) ?
                [...Object.keys(note.content.old_editions).map((keyItem) => { return note.content.old_editions[keyItem] }), data.last_edit]
                : [data.last_edit];

            console.log(old_editions);

            let postObject = {
                id: idNote, //Math.floor(Math.random() * 1001),
                //         title: props.text,
                //         theme: currentProjectTitle,
                //         //    //     answer: value,
                //         comment: currentProjectMediaAndDataAndTemplatesURL, //
                type: 'sciencenotes',
                content: { ...data, old_editions, tags },
                //         quizString: '   ' + currentProjectTitle + '   ' + currentProjectSourseDataURL,
                deleted: false,
                email: email,
                user: user,
                avatarUrl: !!avatarUrl ? avatarUrl : null,
                date: new Intl.DateTimeFormat("ru", {
                    weekday: "short",
                    year: "numeric",
                    month: "short",
                    day: "numeric",
                    hour: "numeric",
                    minute: "numeric",
                }).format(new Date()), //Date().toJSON()
            };

            console.log(postObject);

            var updates = {};
            updates["usersScience/" + userEmail + "/notes/" + idNote] = postObject;

            return firebase.database().ref().update(updates).then(() => {
                setSavedAnswer(true);
                setTimeout(window.location.reload(), 8000);
            });

        }
    }




    return <FormProvider {...methods}>

        {screenSize === "large" ?
            <Container>
                <ButtonGroup aria-label="Basic example" size="sm">
                    <Button variant="outline-secondary" onClick={() => setCurrentField("h1")}>h1</Button>
                    <Button variant="outline-secondary" onClick={() => setCurrentField("h2")}>h2</Button>
                    <Button variant="outline-secondary" onClick={() => setCurrentField("h3")}>h3</Button>
                    <Button variant="outline-secondary" onClick={() => setCurrentField("h4")}>h4</Button>
                    <Button variant="outline-secondary" onClick={() => setCurrentField("h5")}>h5</Button>
                    <Button variant="outline-secondary" onClick={() => setCurrentField("h6")}>h6</Button>
                    <Button variant="outline-secondary" onClick={() => setCurrentField("last_edit")}>Last Edit</Button>
                    <Button variant="outline-secondary" onClick={() => setCurrentField("cited_reference")}>Cited Reference</Button>
                    <Button variant="outline-secondary" onClick={() => setCurrentField("plagiarism")}>Plagiarism</Button>
                </ButtonGroup>
                <Form onSubmit={handleSubmit(onSubmit)}>
                    {(() => {
                        switch (currentField) {
                            case "h1": return <ActiveField key={currentField} name={currentField} label={" 1"} />;
                            case "h2": return <ActiveField key={currentField} name={currentField} label={" 2"} />;
                            case "h3": return <ActiveField key={currentField} name={currentField} label={" 3"} />;
                            case "h4": return <ActiveField key={currentField} name={currentField} label={" 4"} />;
                            case "h5": return <ActiveField key={currentField} name={currentField} label={" 5"} />;
                            case "h6": return <ActiveField key={currentField} name={currentField} label={" 6"} />;
                            case "last_edit": return <ActiveField key={currentField} name={currentField} label={""} />;
                            case "cited_reference": return <ActiveField key={currentField} name={currentField} label={""} />;
                            case "plagiarism": return <ActiveField key={currentField} name={currentField} label={""} />;
                        }
                    })()}

                    <CreatableSelectComponent tags={note.content.tags} updateTags={updateTags}/>

                    <Button
                        variant="outline-secondary"
                        type="submit"
                        size="sm"
                        block
                        disabled={savedAnswer}
                    >
                        
                    </Button>
                </Form>



            </Container> :
            <Container>
                <Form onSubmit={handleSubmit(onSubmit)}>
                    <ActiveField name={"h6"} label={""} />
                    <ActiveField name={"last_edit"} label={""} />
                    <ActiveField name={'cited_reference'} label={""} />
                    <ActiveField name={'plagiarism'} label={""} />
                    <Button
                        variant="outline-secondary"
                        type="submit"
                        size="sm"
                        block
                        disabled={savedAnswer}
                    >
                        
                    </Button>

                </Form>

            </Container>
        }



        {/* {formFields.map((item) =>
                <ActiveField key={item.name} name={item.name} />
            )} */}

    </FormProvider>
}

function ActiveField({ name, label = null }) {
    const methods = useFormContext();
    return <Form.Group controlId={"field_" + name}>
        <Form.Label className="small text-muted">{!!label ? label : name}</Form.Label>
        <Form.Control
            {...methods.register(name)}
            as="textarea"
            rows="3"
        />
        {/* <Form.Text className="text-muted">: =NPV(0.1;100,200,300)-500</Form.Text> */}
    </Form.Group>
}
