import React from "react";
import { useForm } from "react-hook-form";
import { useFirebaseNode } from "../hooks/useFirebaseNode";
import { useSelector } from "react-redux";

import Layout from "../components/layout";

//import DeleteFireNodeButton from "../components/DeleteFireNodeButton";
// import MarkDeleteFireNodeButton from "../components/MarkDeleteFireNodeButton";

import { selectApplication } from "../features/application/applicationSlice";
import useMedia from "../hooks/useMedia";
