// import MediaTemplatesAccountingWithProfitsCash from "../mediatemplates/MediaTemplatesAccountingWithProfitsCash";

import ScienceNotesEditor from "../mediatemplates/ScienceNotesEditor";




import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import Container from "react-bootstrap/Container";
import Form from "react-bootstrap/Form";
import FormControl from "react-bootstrap/FormControl";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";


import { SVGBootstrapIconPencilSquare, SVGBootstrapIconSearch, SVGBootstrapIconEnvelopeOpen } from "../svgs";


import ReactHtmlParser from 'react-html-parser'; //, { processNodes, convertNodeToElement, htmlparser2 }

import "../mediatemplates/ScienceNotes.css";



function cardTitle(content) {
  if (!!content?.h6) { return content.h6 }
  if (!!content?.h5) { return content.h5 }
  if (!!content?.h4) { return content.h4 }
  if (!!content?.h3) { return content.h3 }
  if (!!content?.h2) { return content.h2 }
  if (!!content?.h1) { return content.h1 }
  return ""
}

export default function MyScienceNotes() {
  // const [data, setData] = React.useState(null);
  const email = useSelector(selectApplication).email;
  let userEmail = email.replace(/[^a-zA-Z0-9]/g, "_");
  //const avatarUrl = useSelector(selectApplication).avatarUrl;
  //const user = useSelector(selectApplication).user;
  const [currentEditId, setCurrentEditId] = React.useState(null);
  const [currentFullMode, setCurrentFullMode] = React.useState(null);

  const { data: userNotes, loading, error } = useFirebaseNode(
    "usersScience/" + userEmail + "/notes/"
  );
  const screenSize = useMedia(
    // Media queries
    ["(min-width: 810px)", "(min-width: 400px)", "(min-width: 100px)"],
    // Column counts (relates to above media queries by array index)
    ["large", "medium", "small"],
    // Default column count
    "large"
  );

  const { register, handleSubmit } = useForm();

  const onSubmit = data => console.log(data);

  if (!!loading || !userNotes) { return <Layout>...</Layout> }
  if (!!error) { return <Layout>:-(</Layout> }

  let notes = Object.keys(userNotes).map((item) => { return userNotes[item] });
  
  function toggleFullMode(id) {
    if (currentFullMode === id) { setCurrentFullMode(null) }
    else { setCurrentFullMode(id) }
  }
  

  if (!!currentEditId) {
    return <Layout>
      <ScienceNotesEditor note={currentEditId === "new" ? { id: "new" } : notes.filter(note => note.id === currentEditId)[0]} screenSize={screenSize} />
    </Layout>
    //    return <SmallScreenCalculations />;
  }


  if (screenSize !== "large") {
    return <Layout>
      <Navbar bg="light" expand="sm" sticky="top" className="mb-2 justify-content-between">
        <Navbar.Brand >Notes</Navbar.Brand>
        <Button size="sm" variant="outline-secondary" onClick={() => setCurrentEditId("new")}>+</Button>

        <Form inline onSubmit={handleSubmit(onSubmit)} className="mt-0 mb-0">
          <FormControl {...register("search_string")} className="mr-sm-2" />
          <Button size="sm" variant="outline-secondary" type="submit"><SVGBootstrapIconSearch /></Button>
        </Form>
      </Navbar>
      {!!notes && notes.map(note =>
        <div key={note.id}>
          <small className={note.id === currentFullMode ? "" : "line-clamp"} >{ReactHtmlParser(note.content.last_edit)}</small>
          <br />
          <Button variant="outline-light" size="sm" onClick={() => setCurrentEditId(note.id)}> <SVGBootstrapIconPencilSquare /></Button>
          <Button variant="outline-light" size="sm" onClick={() => toggleFullMode(note.id)}> <SVGBootstrapIconEnvelopeOpen /></Button>
          <cite title="cited_reference"><small>{note.content.cited_reference}</small></cite>
          {"   "}
          <cite title="plagiarism"><small>{note.date+ "   "}  {note.content.plagiarism}</small></cite>

          <hr />
        </div>

      )}
    </Layout>
    //    return <SmallScreenCalculations />;
  }

  return (
    <Layout>

      <Navbar bg="light" expand="sm" sticky="top" className="mb-2 justify-content-between">
        <Navbar.Brand >Notes</Navbar.Brand>
        <Button size="sm" variant="outline-secondary" onClick={() => setCurrentEditId("new")}>+</Button>

        <Form inline onSubmit={handleSubmit(onSubmit)} className="mt-0 mb-0">
          <FormControl {...register("search_string")} className="mr-sm-2" />
          <Button size="sm" variant="outline-secondary" type="submit"><SVGBootstrapIconSearch /></Button>
        </Form>
      </Navbar>

      <Container>
        {!!notes && notes.map(note =>
          <Card key={note.id} className="mb-1">
            <Card.Header>
              <Button variant="outline-light" size="sm" onClick={() => setCurrentEditId(note.id)}> <SVGBootstrapIconPencilSquare /></Button>
              <Button variant="outline-light" size="sm" onClick={() => toggleFullMode(note.id)}> <SVGBootstrapIconEnvelopeOpen /></Button>
              <span className="ml-2" >{cardTitle(note.content)}</span>
            </Card.Header>
            <Card.Body>
              {/* <Card.Title>Special title treatment</Card.Title> */}
              <blockquote className="blockquote mb-0">
                <small className={note.id === currentFullMode ? "" : "line-clamp"}>{!!note?.content?.last_edit ? ReactHtmlParser(note.content.last_edit) : "-"}</small>

                {!!note?.content?.cited_reference ?
                  <footer className="blockquote-footer">
                    <cite title="ited_Reference"> {note.content.cited_reference}</cite>
                  </footer>
                  : null}

                {!!note?.content?.plagiarism ?
                  <footer className="blockquote-footer">
                    <cite title="Plagiarism">{note.date + "   "} {note.content.plagiarism} </cite>
                  </footer>
                  : null}

              </blockquote>
              {/* <Card.Text>{note.content.h3}</Card.Text> */}

            </Card.Body>
          </Card>
        )}
      </Container>
    </Layout>
  );
}

